.App {
  text-align: center;
  height: 100vh;
  margin: 2rem 0 0;
}

html {
  background-color: antiquewhite;
  font-family: Avenir Next, sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.container {
  display: -ms-flexbox;
  display: flex;
  justify-items:center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 1em;
}

@media only screen and (max-width: 650px) {
  .container {
    -ms-flex-direction: column;
        flex-direction: column;
    justify-items: stretch;
    -ms-flex-align: stretch;
        align-items: stretch;
  }
}

button {
  border: solid 1px  rgb(67, 36, 67);
  border-radius: 4px;
  color: antiquewhite;
  font-weight: 600;
  padding: 1em 2em;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1em;
  margin: .25em;
  cursor: pointer;
  -webkit-transition: background-color 0.4s ease-in-out, color 0.6s ease-in, -webkit-box-shadow 0.4s ease-out;
  transition: background-color 0.4s ease-in-out, color 0.6s ease-in, -webkit-box-shadow 0.4s ease-out;
  -o-transition: background-color 0.4s ease-in-out, box-shadow 0.4s ease-out, color 0.6s ease-in;
  transition: background-color 0.4s ease-in-out, box-shadow 0.4s ease-out, color 0.6s ease-in;
  transition: background-color 0.4s ease-in-out, box-shadow 0.4s ease-out, color 0.6s ease-in, -webkit-box-shadow 0.4s ease-out;
}
 
button:hover {
  -webkit-box-shadow: 0 .25em .5em 0 midnightblue, 0 .5em .5em 0 midnightblue;
          box-shadow: 0 .25em .5em 0 midnightblue, 0 .5em .5em 0 midnightblue;
  background-color: rgb(67, 36, 67);
  color: ghostwhite;
}




