.Person {
  min-width: 15vw;
  min-height: 18vw;
  margin: 2rem 1.5vw;
  border: 1px solid  rgb(67, 36, 67);
  border-radius: 4px;
  -webkit-box-shadow: 0 2px 3px midnightblue;
          box-shadow: 0 2px 3px midnightblue;
  padding: 2em;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #222;
  -webkit-transition: color 0.4s ease-in-out;
  -o-transition: color 0.4s ease-in-out;
  transition: color 0.4s ease-in-out;
}

.Person:hover {
  color: rgb(150, 21, 42);
  cursor: pointer;
}


@media only screen and (max-width: 650px) {
  .Person {
    margin: 1.5em auto;
    height: 45vw;
    width: 90%;

  }
}

